<template>
    <a :href="href">{{name}}</a>
</template>

<script>
export default {
    props: {
        uid: {type: String, required: true},
        name: {type: String, required: true}
    },
    computed: {
        href() {
            return 'https://www.uib.no/user/uib/' + this.uid;
        }
    }
}
</script>