<template>
    <section class="team-page content">
        <h1>Driftsteamene og tilhørende tjenester</h1>
        <div v-for="team in teams" :key="team.title" class="team">
            <h2 @click="team.shown = !team.shown">{{team.title}}

                <span class="service-count">({{team.services.length}})</span>

                &nbsp;<fa-icon :name="'angle-' + (team.shown ? 'down' : 'right')"/>

            </h2>
            <ul v-if="team.shown" class="services">
                <li class="service" v-for="svc in team.services" :key="svc.id">
                    <router-link :to="'/node/' + svc.id">{{svc.title}}</router-link>
                    <div class="operators" v-if="svc.ops.length">
                        Driftsansvarlig:
                        <ul>
                           <li class="operator" v-for="op in svc.ops" :key="op.uid"><user v-bind="op"/></li>
                        </ul>
                    </div>
                </li>
            </ul>
        </div>
    </section>
</template>

<script>
import tkdata from '@/tkdata';
import FaIcon from '@/components/FaIcon.vue'
import User from '@/components/User.vue'

export default {
    data() {
        return {
            'teams': [],
        }
    },
    created() {
        tkdata().then(data => {
            const team = {};
            Object.values(data.service).forEach(svc => {
                if (svc.info.service_state == 'avsluttet')
                    return;
                const team_title = (svc.related && svc.related.op_team && svc.related.op_team.title) || 'Teamløs';
                if (!(team_title in team)) {
                    team[team_title] = {
                        title: team_title,
                        services: [],
                        shown: false,
                    }
                }
                team[team_title].services.push({
                    id: svc.id,
                    title: svc.title.trim(),
                    ops: (svc.role && svc.role.operator) || [],
                });
            });
            const teams = Object.values(team);
            teams.sort((a,b) => a.title.localeCompare(b.title, 'nb'));
            teams.forEach(team => {
                team.services.sort((a,b) => a.title.localeCompare(b.title, 'nb'));
            });
            this.teams = teams;
        });
    },
    components: {
        FaIcon,
        User,
    },
};
</script>

